import React from 'react'
import Link from 'gatsby-link'
import { withPrefix } from 'gatsby-link'

class Header extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.isHomepage = location.pathname === withPrefix('/')
  }

  render() {
    return (
      <div>
      <div
        style={{
          margin: '0 auto',
            maxWidth: 960,
            padding: '1.45rem 1.0875rem',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        }}
      >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            textDecoration: 'none',
            color: '#ff6f43',
          }}
        >
          {this.props.siteTitle} {this.isHomepage ? 'v3' : 'v2'}
        </Link>
      </h1>
      <div
        style={{
          display: 'inline-block',
          float: 'right',
        }}
      >
        {this.isHomepage ? (
          <Link
            to="/v2"
            style={{
              color: 'white',
              paddingLeft: '16px',
            }}
          >
            V2
          </Link>
        ) : (
          <Link
            to="/"
            style={{
              color: 'white',
              paddingLeft: '16px',
            }}
          >
            V3
          </Link>
        )}
      </div>
    </div>
      </div>
    )
  }
}

export default Header



